import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter);

import Hello from '../pages/Hello.vue';
const router = new VueRouter({
    /*mode: 'history',
    scrollBehavior,*/
    routes: [
        // { path: '/', redirect: '/personal' },
        { path: '/', component: Hello},
    ]
})
export default router;


